button:focus {outline:0;}

.header {
  background-color: #5FA535;
  text-align: center;
  padding: 5px;
  height: 60px;
  color: white;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.title {
  font-size: 1.5em;
}

.game-area {
  padding-top: 10px;
  margin: auto;
  width: 500px;
  -webkit-perspective: 4000px;
          perspective: 4000px;
}

.game-controls {
  text-align: center;
  margin: auto;
  margin-bottom: 10px;
}

#message-area {
  z-index: -10;
  padding-top: 600px;
  position: relative;
  color: black;
  text-align: center;
  font-size: 20px;
}

.button {
  background-color: #5FA535;
  font-size: 12px;
  color: white;
  width: 100px;
  height: 25px;
  padding-left: 5px;
  padding-right: 5px;
  margin-right: 5px;
  margin-left: 5px;
  cursor: pointer;
}

.flip {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

#boards {
  z-index: 1000;
  -webkit-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.one, .two {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
	top: 0;
	left: -8px;
  width: 500px;
  height: 550px;
  border-width: 8px;
  border-style: solid;
  margin: auto;
  text-align: justify;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.one {
  z-index: 2;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.two {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}



